import React from "react";

export default class Tab extends React.Component {
  componentDidMount() {
    this.props.addTab({
      id: this.props.id,
      title: this.props.title,
      shortTitle: this.props.shortTitle
    });
  }

  render() {
    return this.props.activeTab.id === this.props.id && this.props.children;
  }
}
