import React, {Fragment} from 'react';

export default class GraphBody extends React.Component {
  // resolution in x and y directions
  static defaultProps = { resolution: [100, 100] };

  constructor(props) {
    super(props);
    this.state = {
      avgText: 'x̄'
    }
    this.onMouseEnter = this.onMouseEnter.bind(this)
    this.onMouseLeave = this.onMouseLeave.bind(this)
  }

  onMouseEnter() {
    this.setState({avgText: "average"})
  }

  onMouseLeave() {
    this.setState({avgText: "x̄"})
  }

  preparePolygon() {
    // [right top, right btm, left bottom, otherpoints...]
    let points = [`${this.props.x+this.props.width},${this.props.y-this.props.height} ${this.props.x+this.props.width},${this.props.y} ${this.props.x},${this.props.y}`];

    let collector = this.props.data.map(coord => {
      const [x, y] = this.props.coordsToSvgCoords(coord)
      return ` ${x},${y}`;
    });

    return points.concat(collector).join(' ')
  }

  render() {
    let points = this.preparePolygon()
    let [x1, y1] = this.props.coordsToSvgCoords([this.props.data.mean, 0])
    let [x2, y2] = this.props.coordsToSvgCoords([this.props.data.mean, 100])
    return(
      <Fragment>
        <polygon points={points} />
        <line x1={x1} y1={y1} x2={x2} y2={y2} className="stroke-current text-gray-500 stroke-1" strokeDasharray=".5 .5"/>
        <text
          x={x1}
          y={8}
          textAnchor="middle"
          style={{fontSize:".5rem", fontFamily:"auto"}}
          className="text-gray-500 select-none"
          onMouseEnter={this.onMouseEnter}
          onMouseLeave={this.onMouseLeave}>
          {this.state.avgText}
        </text>
      </Fragment>
      )
  }
}
